<template>
  <div class="centerRight list_box">
    <div id="centerRight"></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      option: {},
    };
  },
  methods: {
    createData() {
      var chartDom = document.getElementById("centerRight");
      var myChart = this.$echarts.init(chartDom);
      myChart.clear();
      var option;
      option = {
         title: {
          text: "重点用能设备用电量统计",
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        grid: {
          top: "25%",
          left: "8%",
          right: "3%",
          bottom: "15%",
        },
        tooltip: {
          trigger: "axis",
        },
        xAxis: {
          data: [
            "设备01",
            "设备02",
            "设备03",
            "设备04",
            "设备05",
            "设备06",
            "设备07",
            "设备08",
          ],

          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            interval: 0,
            color: "#fff",
          },
          axisTick: {
            //坐标轴刻度线
            show: false,
          },
        },
        yAxis: {
          name: "(KW)",
          max: function (value) {
            return value.max * 1.2;
          },
          nameTextStyle: {
            color: "#fff",
            fontSize: 12,
          },
          type: "value",
          splitLine: {
            show: false, //去掉网格线
          },

          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            color: "#fff",
          },
        },
        series: [
          {
            data: [5, 6, 18, 24, 13, 35, 28,7],
            label: {
              show: true,
              position: "top",
              color: "#fff",
            },
            type: "bar",
            barWidth:40,
            itemStyle: {
              normal: {
                color: "#87e8de",
              },
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData();
  },
};
</script>

<style lang="less" scoped>
#centerRight {
  width: 100%;
  height: calc(100% - 20px);
 
}
.centerRight {
  width: 100%;
  padding-top: 20px;

}
</style>
